/*General styles*/

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Turret Road', cursive;
}

a{
  text-decoration: none;
}



body {
  line-height: 1;
  font-size: 20px;
}

.subtitle-container{
  display: flex;
  padding-top: 35px;
  justify-content: center;
  text-align: center;
  align-items: center;
}
.sub-title{
  text-align: center;
  letter-spacing: 2px;
  font-weight: 100;
  font-size: 28px;
  padding: 0 12px;
  font-family: 'Orbitron', sans-serif;
}

/* Buttons*/
/*Btn primary starts here*/
.btn-primary{

  color: #333;
  text-transform:uppercase;
  font-family: 'Orbitron', sans-serif;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 1px;;
}


/*main button styles*/
.btn-primary{
  font-size: 20px;
  font-weight: bolder;
  letter-spacing: 3px;
  position: relative;
  padding: 6px 30px;
  text-decoration: none;
  color: #fff;
  background-color: transparent;
  border: none;
  cursor: pointer;
  
}

.btn-primary:before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  border-top: 1px white solid;
  border-left: 1px white solid;
  border-bottom: 6px black solid;
  opacity: 1;
  transition: all 0.6s 0.3s;
}

.btn-primary:after{
  content: "";
  position: absolute;
  top: 0;
  right: 0;
   width: 50%;
  height: 100%;
  border-top: 1px white solid;
  border-right: 1px white solid;
  border-bottom: 6px black solid;
  opacity: 1;
  transition: all 0.6s 0.3s;
}

.btn-primary:hover:before{
  transform: translateX(-50%);
  opacity: 0;
}

.btn-primary:hover:after{
  transform: translateX(50%);
  opacity: 0;
}


.btn-span{
  position: absolute;
  top:0;
  left: 50%;
  background: white;
  width: 1px;
  height: 100px;
  transform-origin: center;
  transform: TranslateY(-100px);
  z-index: 1;
  transition: all 0.8s;
  opacity: 0;
}

.text-container button:hover span{
  opacity: 1;
  transform: translateY(1px);
  animation: hide 0.7s linear forwards;
}

.btn-checkout-box button:hover span{
  opacity: 1;
  transform: translateY(1px);
  animation: hide 0.7s linear forwards;
}


@keyframes hide {
    to {
        opacity: 0;
    }
}


.btn-secondary{
  padding: 5px 30px;
  color: black;
  font-family: 'Gruppo', cursive;
  background: linear-gradient(to bottom, rgb(49, 207, 154), rgb(29, 211, 243));
  border-radius: 20px;
  text-align: center;
}

.btn-secondary:hover{
